<template>
	<div>
		<Markdown>
			<h1>Dropdown 下拉菜单</h1>
			<p>向下弹出的菜单列表。</p>
			<h2>何时使用</h2>
			<p>当页面上的操作命令过多时，用此组件可以收纳操作元素。点击或移入触点，会出现一个下拉菜单。可在列表中进行选择，并执行相应的命令。</p>
			<h2>代码演示</h2>
		</Markdown>
		<vui-row :gutter="20">
			<vui-col :span="12">
				<ExampleDropdownBasicUsage />
			</vui-col>
			<vui-col :span="12">

			</vui-col>
		</vui-row>
	</div>
</template>

<script>
	import MixinCatalogue from "@/mixins/catalogue";
	import Markdown from "@/components/markdown";
	import ExampleDropdownBasicUsage from "./examples/basic-usage";

	export default {
		mixins: [
			MixinCatalogue
		],
		components: {
			Markdown,
			ExampleDropdownBasicUsage,
		}
	};
</script>


