<template>
	<Example vertical id="example-dropdown-basic-usage">
		<template slot="source">
			<vui-dropdown>
				<vui-button>Dropdown</vui-button>
				<vui-dropdown-menu slot="menu" @click="handleClick">
					<vui-dropdown-submenu name="1" icon="apps" title="Item 1">
						<vui-dropdown-menu-item name="1-1" title="Item 1-1" />
						<vui-dropdown-menu-item name="1-2" title="Item 1-2" />
						<vui-dropdown-menu-item name="1-3" title="Item 1-3" />
						<vui-dropdown-menu-item name="1-4" title="Item 1-4" />
					</vui-dropdown-submenu>
					<vui-dropdown-menu-item name="2" icon="mail" title="Item 2" />
					<vui-dropdown-submenu name="3" icon="settings" title="Item 2">
						<vui-dropdown-menu-item-group title="Group 1">
							<vui-dropdown-menu-item name="3-1" title="Item 3-1" />
							<vui-dropdown-menu-item name="3-2" title="Item 3-2" />
						</vui-dropdown-menu-item-group>
						<vui-dropdown-menu-item-group title="Group 2">
							<vui-dropdown-menu-item name="3-2" title="Item 3-2" />
							<vui-dropdown-menu-item name="3-4" title="Item 3-4" />
						</vui-dropdown-menu-item-group>
					</vui-dropdown-submenu>
					<vui-dropdown-submenu name="4" icon="links" title="Item 4">
						<vui-dropdown-menu-item name="4-1" title="Item 4-1" />
						<vui-dropdown-menu-item name="4-2" title="Item 4-2" />
						<vui-dropdown-submenu name="4-3" title="Item 4-3">
							<vui-dropdown-menu-item name="4-3-1" title="Item 4-3-1" />
							<vui-dropdown-menu-item name="4-3-2" title="Item 4-3-2" />
						</vui-dropdown-submenu>
					</vui-dropdown-submenu>
				</vui-dropdown-menu>
			</vui-dropdown>
		</template>
		<template slot="title">基本用法</template>
		<template slot="describe">
			<p>最简单的下拉菜单。</p>
		</template>
		<template slot="code">{{code}}</template>
	</Example>
</template>

<script>
	import Example from "@/components/example";
	import code from "./code";

	export default {
		components: {
			Example
		},
		data() {
			return {
				code
			};
		},
		methods: {
			handleClick(name) {
				this.$message.info("Click Menu Item " + name);
			}
		}
	};
</script>



